import React from 'react';
import PropTypes from 'prop-types';

import { SQUARES, PIECES } from 'chess/utils/constants/constants';
import DumbPiece from './piece';

export default function SmartPiece(props) {
  return <DumbPiece {...props} />;
}

SmartPiece.propTypes = {
  dnd: PropTypes.bool.isRequired,
  canMove: PropTypes.bool.isRequired,
  onPress: PropTypes.func.isRequired,
  square: PropTypes.oneOf(SQUARES).isRequired,
  piece: PropTypes.oneOf(PIECES).isRequired,
};



