import ChessEngine from 'chess/utils/chess';
import { movePiece } from 'chess/actions';

import {
  START_GAME,
  END_GAME,
  MOVE_UI,
  MOVE_AI,
  MOVES_LOAD,
  BAD_MOVES_LOAD,
  BAD_MOVE,
} from '../actions/constants';

let game = null;

export function validateMove(from, to) {
  if (!game) {
    return false;
  }
  return game.validate(from, to);
}

export const chessMiddleware = store => next => action => {
  switch (action.type) {
    case START_GAME: {
      // move to chess
      let { fen, moves, difficulty, expertise } = action;
      game = new ChessEngine(fen, difficulty, expertise);
      next(action);
      load(store.dispatch)(moves);
      return;
    }

    case END_GAME: {
      // move below line to chess
      game = null;
      break;
    }

    case MOVE_UI: {
      // move to chess
      if (!game) {
        return;
      }

      const { from, to } = action;
      const move = game.move(from, to);
      if (!move) {
        return next({ type: BAD_MOVE, from, to });
      } // bad move
      store.dispatch(movePiece(move));
      return;
    }

    case MOVE_AI: {
      // move to chess
      if (!game) {
        return;
      }

      const move = game.ai();
      store.dispatch(movePiece(move));
      return;
    }

    case MOVES_LOAD: {
      const moves = action.moves;
      load(store.dispatch)(moves);
      return;
    }

    default:
  }

  return next(action);
};

const load = next => moves => {
  if (!moves || moves.length === 0) {
    return;
  }

  const move = game.load(moves); // returns the last move
  if (!move) {
    // bad move sequence
    console.log(moves);
    return next({ type: BAD_MOVES_LOAD, moves });
  }
  next(movePiece(move));
};



