import {
  CLOCK_TIMEOUT,
  RESIGN_GAME,
  MOVE_UI,
  MOVE_AI,
  MOVE_PIECE,
  DRAW_GAME,
  START_GAME,
} from './constants';

export function resignGame(color) {
  return {
    type: RESIGN_GAME,
    color,
  };
}

export function clockTimeout(color) {
  return {
    type: CLOCK_TIMEOUT,
    color,
  };
}

export function drawGame() {
  return {
    type: DRAW_GAME,
  };
}

export function makeMove(from, to) {
  return {
    type: MOVE_UI,
    from,
    to,
  };
}

export function aiMove() {
  return {
    type: MOVE_AI,
  };
}

export function movePiece(move) {
  return {
    type: MOVE_PIECE,
    move,
  };
}

export function startGame(fen) {
  return {
    type: START_GAME,
    fen,
  };
}



