import React from 'react';
import PropTypes from 'prop-types';

import { SQUARES, PIECES } from 'chess/utils/constants/constants.js';

import DumbSquare from './square';
import SmartPiece from '../piece';

export default function SmartSquare(props) {
  const { dnd, piece, square, getPossibleMoves, isColorToMove, pickSquare } = props;

  const isBlack = (square.charCodeAt(0) + square.charCodeAt(1)) % 2 === 0;
  const canMoveFrom = Boolean(piece && getPossibleMoves(square).length > 0);
  const onPickSquare = () => pickSquare(square, isColorToMove);

  const children = piece && (
    <SmartPiece
      dnd={dnd}
      canMove={canMoveFrom}
      square={square}
      piece={piece}
      onPress={onPickSquare}
    />
  );

  return (
    <DumbSquare
      canMoveTo={props.canMoveTo}
      isOver={props.isOver}
      files={props.files}
      oppMove={props.oppMove}
      isSelected={props.isSelected}
      isInDanger={props.isInDanger}
      isValidMove={props.isValidMove}
      isBlack={isBlack}
      onPress={onPickSquare}>
      {children}
    </DumbSquare>
  );
}

SmartSquare.propTypes = {
  dnd: PropTypes.bool.isRequired,
  square: PropTypes.oneOf(SQUARES).isRequired,
  piece: PropTypes.oneOf(PIECES),
  canMoveTo: PropTypes.bool.isRequired,
  oppMove: PropTypes.number.isRequired,
  isSelected: PropTypes.bool.isRequired,
  files: PropTypes.number.isRequired,
  pickSquare: PropTypes.func.isRequired,
  isInDanger: PropTypes.bool,
  isValidMove: PropTypes.func.isRequired,
  getPossibleMoves: PropTypes.func.isRequired,
};



