import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Chessboard from 'chess/components/chessboard';
import DumbGame from './game';

export default class SmartGame extends Component {
  static propTypes = {
    fen: PropTypes.string.isRequired,
    orientation: PropTypes.string.isRequired,
  };

  render() {
    const props = this.props;
    const children = (
      <Chessboard dnd={false} lineupOnly orientation={props.orientation} fen={props.fen} />
    );

    return <DumbGame>{children}</DumbGame>;
  }
}




