import user from './user'
import local from './local'
import app from './app'
import {chess} from 'chess/reducers'
import {hiit} from 'hiit/reducer'

const reducers = {
    // below: order is to be maintained
  chess,
  hiit,
  local,
    // any order
  user,
  app,
}

export default reducers



