const piecesMap = {
  p: 'bP',
  c: 'bC',
  b: 'bB',
  n: 'bN',
  r: 'bR',
  q: 'bQ',
  k: 'bK',
  P: 'wP',
  C: 'wC',
  B: 'wB',
  N: 'wN',
  R: 'wR',
  Q: 'wQ',
  K: 'wK',
  '.': null,
};

function addPadding(fen) {
  return String(`${fen}....`).slice(0, 8);
}

function removeRowNumbers(row) {
  return row.replace(/(\d)/g, digit => '.'.repeat(+digit));
}

function getFenArray(fen) {
  return fen
    .split(' ')[0]
    .split('/')
    .map(removeRowNumbers)
    .map(addPadding);
}

export function getPieces(fen) {
  const pieces = [];

  getFenArray(fen).forEach(row => {
    row.split('').forEach(char => pieces.push(piecesMap[char]));
  });

  return pieces;
}

export function countFiles(fen) {
  const row = fen
    .split('/')
    .shift()
    .split('');
  let files = 0;
  row.forEach(p => {
    if (p >= '1' && p <= '9') {
      files += Number(p); // empty slots
    } else {
      files++; // is piece
    }
  });
  return files;
}

export function nextTurnColor(fen) {
  const color = fen.split(' ')[1];
  return color;
}



