import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { DragDropContext } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';

import { COLOR } from 'chess/utils/constants/constants';

@DragDropContext(HTML5Backend)
export default class DumbChessboard extends Component {
  static propTypes = {
    lineupOnly: PropTypes.bool.isRequired,
    files: PropTypes.number.isRequired,
    orientation: PropTypes.oneOf(
      Object.keys(COLOR).map(name => COLOR[name]),
    ).isRequired,
    pickSquare: PropTypes.func,
    children: PropTypes.array,
  }

  onDragAndDropMove(fromSquare, toSquare) { // Drag-and-drop
    this.props.pickSquare(fromSquare);
    this.props.pickSquare(toSquare);
  }

  render() {
    const { lineupOnly, files, children } = this.props;

    const classes = classNames({
      'react-chessboard': true,
      // 'react-chessboard--flipped': orientation === COLOR.BLACK,
      'react-chessboard--files-4': !lineupOnly && files === 4,
      'react-chessboard--files-5': !lineupOnly && files === 5,
      'react-chessboard--files-6': !lineupOnly && files === 6,
      'react-chessboard--lineup-4': lineupOnly && files === 4,
      'react-chessboard--lineup-5': lineupOnly && files === 5,
      'react-chessboard--lineup-6': lineupOnly && files === 6,
    });

    return (
      <div className={classes}>
        {children}
      </div>
    );
  }
}



