import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { isWhite, COLOR, SQUARES } from 'chess/utils/constants/constants';
import { countFiles, getPieces } from 'chess/utils/fen/fen';

import SmartSquare from 'chess/components/square';
import DumbChessboard from './chessboard';

// helper - returns [ start, end, direction ]
function getChessDirection(lineupOnly, orientation) {
  return lineupOnly
    ? isWhite(orientation)
      ? [48, 64, +1]
      : [15, -1, -1]
    : isWhite(orientation)
      ? [0, 64, +1]
      : [63, -1, -1];
}

export default class SmartChessboard extends Component {
  static propTypes = {
    dnd: PropTypes.bool.isRequired,
    lineupOnly: PropTypes.bool.isRequired,
    orientation: PropTypes.oneOf(Object.keys(COLOR).map(name => COLOR[name])).isRequired,
    fen: PropTypes.string.isRequired,
    inCheck: PropTypes.bool,
    colorToMove: PropTypes.string,
    highlighted: PropTypes.array,
    getPossibleMoves: PropTypes.func,
    isValidMove: PropTypes.func,
    onMove: PropTypes.func,
  };

  static defaultProps = {
    highlighted: [],
    onMove: () => false,
    isValidMove: () => false,
    getPossibleMoves: () => [],
  };

  constructor(props) {
    super(props);
    this.state = {
      pieces: null, // from fen
      files: 0, // from fen
      fromSquare: null,
    };
  }

  pickFromSquare(fromSquare) {
    this.setState({ fromSquare });
  }

  pickToSquare(toSquare) {
    const isValid = this.props.isValidMove(this.state.fromSquare, toSquare);
    if (isValid) {
      this.props.onMove(this.state.fromSquare, toSquare); // tell computer to seek a move
      this.setState({ fromSquare: null });
    } else {
      // window.alert(strings.errInvalidSelection.join("\n"));
    }
  }

  pickSquare(square, isMyColor) {
    console.log('pickSquare ' + square);
    if (this.props.lineupOnly) {
      return;
    } // no selection in line-ups
    const { fromSquare } = this.state;
    if (isMyColor || !fromSquare) {
      return this.pickFromSquare(square); // we should not be here
    } else {
      return this.pickToSquare(square);
    }
  }

  children() {
    const {
      dnd,
      inCheck,
      lineupOnly,
      orientation,
      colorToMove,
      highlighted,
      isValidMove,
      getPossibleMoves,
    } = this.props;
    const { files, pieces } = this.state;
    const fromSquare = this.state.fromSquare;
    const pickSquare = this.pickSquare.bind(this);
    const squares = [];

    let theValidMoves = getPossibleMoves(fromSquare);

    /* If lineupOnly is true, show only first/last ranks with pieces.
     * For black pieces, start at 0 and for white pices start at 48.
     * This is because, white is always placed toards rank 1.
     */
    const [start, end, direction] = getChessDirection(lineupOnly, orientation);
    const colorPrefix = !lineupOnly && colorToMove[0].toLowerCase();

    let index = start;
    do {
      const piece = pieces[index];
      const square = SQUARES[index];

      // beyond our chess board
      if (index % 8 < files) {
        const oppMove = highlighted.indexOf(square) + 1; // 0 - not, 1 - from, 2 - to
        const isSelected = square === fromSquare;
        const canMoveTo = theValidMoves.indexOf(square) >= 0;

        const isColorToMove = piece && piece.split('')[0] === colorPrefix;
        const isInDanger = inCheck && !lineupOnly && piece === `${colorPrefix}K`;

        squares.push(
          <SmartSquare
            square={square}
            key={square}
            files={files}
            pickSquare={pickSquare}
            canMoveTo={canMoveTo}
            isSelected={isSelected}
            oppMove={oppMove}
            isInDanger={isInDanger}
            dnd={dnd}
            piece={piece}
            isColorToMove={isColorToMove}
            isValidMove={isValidMove}
            getPossibleMoves={getPossibleMoves}
          />
        );
      }
      index += direction;
    } while (index !== end);

    return squares;
  }

  componentWillMount() {
    this.prepareComponentState(this.props);
  }

  componentWillReceiveProps(nextProps) {
    this.prepareComponentState(nextProps);
  }

  prepareComponentState(props) {
    const { fen } = props;
    const pieces = getPieces(fen);
    const files = countFiles(fen);
    this.setState({ files, pieces });
  }

  render() {
    const { lineupOnly, orientation } = this.props;
    const files = this.state.files;
    const squares = this.children();

    return (
      <DumbChessboard lineupOnly={lineupOnly} files={files} orientation={orientation}>
        {squares}
      </DumbChessboard>
    );
  }
}



