
import {
    LOGIN_ATTEMPT, LOGIN_SUCCESS, LOGIN_FAILED,
    PROFILE_ATTEMPT, PROFILE_SUCCESS, PROFILE_FAILED,
    LOGOUT_ATTEMPT, LOGOUT_SUCCESS, LOGOUT_FAILED,
    LOCK_UI, UNLOCK_UI,
  } from './constants';

import { registerFbTokenApi, getProfileApi,
    logoutUserApi, } from 'app/utils/usersApi';

import {fbLoginApi, fbLogoutApi} from 'app/utils/fbAuth';

const isCordovaApp = !!window.cordova;

export function fbLogin(successUrl) {
  return (dispatch) => {
    // console.info('In cordovaLogin');
    // start login
    dispatch({ type: LOGIN_ATTEMPT });

    return fbLoginApi(successUrl)
    .then(token => {
      // in case of mobile apps
      if (token) return registerFbTokenApi(token)
     })
    .then(() => { // (response)
      dispatch({ type: LOGIN_SUCCESS })
      if (isCordovaApp) return getProfile()(dispatch)
    })
    .catch((error) => {
      console.log(error)
      dispatch({ type: LOGIN_FAILED, error });
    })
  }
}

export function getProfile() {
  return (dispatch) => {
    // start login
    dispatch({ type: LOCK_UI })
    dispatch({ type: PROFILE_ATTEMPT })
    return getProfileApi()
    .then((response) => {
      const data = response.data
      let quests = {}
      data.quests.forEach(({ quest, level, expertise }) => {
          quests[quest] = { level, expertise };
      })
      delete data.quests
      dispatch({ type: PROFILE_SUCCESS, ...data, quests })
    })
    .catch((error) => {
      console.error(error)
      dispatch({ type: PROFILE_FAILED, error })
    })
    .then(() => dispatch({ type: UNLOCK_UI }))
  }
}

export function logoutUser() {
  return (dispatch) => {
    //dispatch({ type: LOCK_UI })
    dispatch({ type: LOGOUT_ATTEMPT });
    return Promise.all([
        logoutUserApi(),
        fbLogoutApi()
      ])
    .then(() => dispatch({ type: LOGOUT_SUCCESS, }))
    .catch( (error) => dispatch({ type: LOGOUT_FAILED, error }) )
    //.then(() => dispatch({ type: UNLOCK_UI }))
  }
}



