import React from 'react';
import { compose, branch, renderNothing, lifecycle } from 'recompose';
import { connect } from 'react-redux';

import {siren, whistle} from './audio';

function HiitWorkoutOverlay({workout, reps, roundCountdown}) {
  return (
    <div className="Hiit-workout-overlay grass-pattern">
      <div className="Hiit-workoutbox Hiit-rows">
        <span className="Hiit-flash-2x">
            {workout}
          </span>
          <span className="Hiit-flash-2x">
            {reps ? `${reps} Reps` : ''}
          </span>
          <span className="Hiit-flash-3x">
            {Math.floor(roundCountdown)+1}
          </span>
      </div>
    </div>
  )
}

const hideIfNoData = hasNoData =>
  branch(
    hasNoData,
    renderNothing
  )

const enhance = compose(
  connect(({hiit}) => ({
    canPlayChess: hiit.isChess,
    workout: hiit.workout,
    reps: hiit.reps,
    roundCountdown: hiit.roundCountdown,
  })),
  hideIfNoData(({canPlayChess}) => canPlayChess),
  lifecycle({
    componentDidMount() {
      whistle();
    },
    componentWillReceiveProps({roundCountdown}) {
      if (roundCountdown <= 3 && roundCountdown >= 1) {
        siren();
      }
    }
  })
)

export default enhance(HiitWorkoutOverlay)



