// A wrapper for p4wn engine

import { p4_new_game } from './p4wn_engine';
import { countFiles } from './../fen/fen';
import { COLOR } from './../constants/constants';
import { BOARD64, BOARD120, P4MOVE } from './constants';

export function Chess(fen, hardness, expertise) {
  const files = countFiles(fen);
  this.game = p4_new_game(files, fen);
  this.moveCount = 0; // moves made by person player

  if (hardness) {
    // AI player
    this.setDifficulty(hardness, expertise);
  }

  // reset cache
  _cur_moveno = -1;
}

// make a move
Chess.prototype.move = function({ from, to, promotion }) {
  //console.log('Move -->', from, to, ' <---> ', BOARD64[from], BOARD64[to]);
  // convert from board-64 to board-120
  const { flags, ok } = this.game.move(BOARD64[from], BOARD64[to], promotion);
  if (!ok) {
    console.log('bad move');
    return;
  }

  const fen = this.fen();
  const color = this.toPlay();
  this.moveCount++;

  return {
    fen,
    color,
    move: [from, to],
    num: this.moveCount,
    canDraw: Boolean(flags & P4MOVE.OFFER_DRAW), //eslint-disable-line no-bitwise
    isDraw: Boolean(flags & P4MOVE.DRAW), //eslint-disable-line no-bitwise
    inCheck: Boolean(flags & P4MOVE.CHECK), //eslint-disable-line no-bitwise
    isMate: Boolean(flags & P4MOVE.MATE), //eslint-disable-line no-bitwise
  };
};

// legal moves
let _cur_moveno = -1;
let movelist = null;
Chess.prototype.moves = function() {
  if (_cur_moveno !== this.game.moveno) {
    // cached values are invalid
    movelist = this.game.moves();
    _cur_moveno = this.game.moveno;
    // movelist.sort((a,b) => a[0]-b[0]).forEach(x => display(x));
  }
  // convert from board-120 to algebraic
  return movelist.map(([score, from, to]) => ({ from: BOARD120[from], to: BOARD120[to], score }));
};

Chess.prototype.fen = function() {
  return this.game.fen();
};

Chess.prototype.toPlay = function() {
  return this.game.to_play === 0 ? COLOR.WHITE : COLOR.BLACK;
};

Chess.prototype.getMoveCount = function() {
  return this.moveCount;
};

Chess.prototype.canMove = function(fromSquare, toSquare) {
  const legalMoves = this.moves();

  if (typeof toSquare === 'undefined') {
    // return: an array
    return legalMoves.filter(legalMove => legalMove.from === fromSquare).map(move => move.to);
  }

  // return: true or false
  return (
    legalMoves.filter(legalMove => legalMove.from === fromSquare && legalMove.to === toSquare)
      .length > 0
  );
};

// AI functions

Chess.prototype.setDifficulty = function(hardness, expertise) {
  this.minLevel = hardness[0];
  this.maxLevel = hardness[1];

  // NOTE: threshold and midLevel are related to expertise
  //
  // as expertise moves closer to 0, threshold is closer to 1 and more levels below
  //    midLevel are choosen. The game becomes easier. Midlevel also moves closer towards
  //    bottom level, but more slowly.
  //
  // as expertise moves closer to 1, threshold is closer to 0 and more levels above
  //    midLevel are choosen. The game becomes harder. MidLevel also moves closer towards
  //    higher level, but more slowly.

  this.threshold = 1 - expertise;

  const deNorm = expertise - 0.5;
  const ratio = 0.5 + Math.sign(deNorm) * deNorm * deNorm; // reduce variance
  this.midLevel = (this.maxLevel - this.minLevel) * ratio + this.minLevel;

  console.info(
    `Min : ${this.minLevel}, Max: ${this.maxLevel}, Mid: ${this.midLevel}, Threshold: ${
      this.threshold
    }`
  );

  //this.maxTime = Math.min(3000 * this.minLevel, 15000);
  //this.overShot = 0;
};

// makes the move
Chess.prototype.computerMove = function() {
  let level = randomLevel(this.minLevel, this.maxLevel, this.midLevel, this.threshold);

  // workaround: games are slow if level is 6+ and no pieces have been cut
  // moveCount is picked randomly
  level = Math.min(level, maxDepth(this.moveCount));

  const startTime = Date.now();
  const mv = this.game.findmove(level);
  const delta = Date.now() - startTime;

  // increment one level after every 4 moves
  //const level = Math.min(this.minLevel + (this.moveCount >> 2) - this.overShot, this.maxLevel);
  //if (delta > this.maxTime) {
  //  this.overShot++;
  //}
  console.log('Move delta : ', delta, 'level : ', level);

  // display([level, ...mv]);

  const { flags } = this.game.move(mv[0], mv[1]); // missing - promotion piece
  const fen = this.fen();
  const color = this.toPlay();
  //this.moveCount++;

  return {
    fen,
    color,
    num: this.moveCount,
    move: [BOARD120[mv[0]], BOARD120[mv[1]]],
    canDraw: Boolean(flags & P4MOVE.OFFER_DRAW), //eslint-disable-line no-bitwise
    isDraw: Boolean(flags & P4MOVE.DRAW), //eslint-disable-line no-bitwise
    inCheck: Boolean(flags & P4MOVE.CHECK), //eslint-disable-line no-bitwise
    isMate: Boolean(flags & P4MOVE.MATE), //eslint-disable-line no-bitwise
  };
};

// Helper function

// NOTE:
// mid = (max - min) * ratio + min
// we have additionally given ratio to speed up calculation
function randomLevel(min, max, mid, ratio) {
  const rnd = Math.random();
  if (rnd <= ratio) {
    // map rnd between min and mid
    return Math.round((rnd / ratio) * (mid - min) + min);
  } else {
    // map rnd between mid and max
    return Math.round(((rnd - ratio) / (1 - ratio)) * (max - mid) + mid);
  }
}

function maxDepth(x) {
  if (x < 10) {
    return 5;
  } else if (x < 15) {
    return 6;
  } else if (x < 20) {
    return 7;
  }
  return 8;
}

// for debugging

/* eslint-disable */
function display(x) {
  console.log(x[0], BOARD120[x[1]], BOARD120[x[2]]);
}



