// Simple wrapper for chess
// p4wn wrapper is too complicated to touch each time

import { Chess } from './p4wn_wrapper';

// hardness and expertise are optional (not required for 2 player games)
export default function SimpleChess(fen, difficulty, expertise) {
  console.log('SimpleChess - ' + fen);
  this.game = new Chess(fen, difficulty, expertise);
}

SimpleChess.prototype.ai = function() {
  //console.log('SimpleChess.ai')
  const move = this.game.computerMove();
  return move;
};

SimpleChess.prototype.move = function(fromSquare, toSquare) {
  console.log('SimpleChess.move');
  const move = this.game.move({
    from: fromSquare,
    to: toSquare,
  });
  return move;
};

SimpleChess.prototype.load = function(moves) {
  console.log('SimpleChess.load');
  let last;

  moves.forEach(m => {
    last = this.game.move(m);
    if (!last) {
      console.log('BAD at (' + m.from + ' -> ' + m.to + ')');
      throw new Error('Unable to parse all moves');
    } else {
      console.log(' (' + m.from + ' -> ' + m.to + ')');
    }
  });

  return last;
};

SimpleChess.prototype.validate = function(fromSquare, toSquare) {
  return this.game.canMove(fromSquare, toSquare);
};

SimpleChess.prototype.count = function() {
  return this.game.getMoveCount();
};



