import {
  LOCAL_START, LOCAL_END, START_GAME, END_GAME,
} from 'app/actions/constants'
// import { reinforceFen } from 'app/utils/games'

export const aiMiddleware = store => next => action => {
  switch(action.type) {
    case LOCAL_START: {
      const expertise = 1.0;
      const difficulty = [8,10];
      let { fen, timeout, } = action;

      //if (reinforce) {
      //  const {orientation, reinforcePieces,} = action;
      //  fen = reinforceFen(fen, orientation, reinforcePieces);
      //}

      store.dispatch({ type: START_GAME, fen, difficulty, expertise, timeout });
      break; // break - pass action to reducer
    }

    case LOCAL_END: {
      // maintain order of actions
      store.dispatch({ type: END_GAME })
      next( action ); // pass LOCAL_END to reducer]
      return; // passed already
    }

    default:
  }
  return next(action)
}



