
/*
 * 1. In chess mode, each player gets 10 seconds to make a move.
 * 
 * 2. The player loses, if they dont make move in the given time. This 
 *    module raises a `Player-Timerout` callback.
 * 
 * 3. In workout mode, each player has to workout for the given number of 
 *    seconds and chess timer is paused.
 * 
 * 4. In a chess and workout mode, players play chess normally.
 * 
 **/

import rounds from './rounds';

// helpers to manage rounds

let playerNum = 0
let playerTimeouts = [0, 0]

let totalTicks = 0
let roundNum = 0
let roundTicks = 0

const curRound = () => roundNum < rounds.length ? rounds[roundNum] : null
const nextRound = () => roundNum+1 < rounds.length ? rounds[roundNum+1] : null
const advanceRound = () => {
  roundNum += 1
  roundTicks = 0
}
const roundExpired = () => curRound().duration <= roundTicks

// current turn timers

let turnTicks = 1

export const endTurn = () => {
  console.log('endTurn')
  playerNum = (playerNum + 1) % 2
  turnTicks = 1 // turn ticks are 1 at end of turn
}

// ***

const isChessPhase = ({phase}) => (phase.indexOf('Chess') !== -1)
const isWorkoutPhase = ({workout}) => !!workout

const waitForMove = () => (turnTicks > 1)
const curPlayerTimeout = () => playerTimeouts[playerNum]
const incrementturnTicks = () => (++turnTicks > curPlayerTimeout())

const keepPlayingChess = () => {
  let cur = curRound();

  // dont time player with 0 timeout
  if (curPlayerTimeout() === 0) {
    return false
  }

  // in Chess or Chess+Workout phase, while the rounds have not expired
  if (isChessPhase(cur) && !roundExpired()) {
    return true
  }

  // if round has expired
  let next = nextRound();
  if (next && !isChessPhase(next)) {
    return waitForMove() // wait for player to make a move
  }

  // if next round is also chess phase, dont wait
  return false
}

// helpers to manage timers

let timer = null

const resetTimer = () => {
  if (!!timer) {
    clearTimeout(timer)
    timer = null
  }
}

export const resetHiit = () => {
  console.log('resetHiit')
  roundNum = 0
  roundTicks = 0
  turnTicks = 1
  totalTicks = 0
  playerNum = 0
  playerTimeouts = [0, 0]
  resetTimer()
}

export const startHiit = (timeouts, clockCb) => {
  console.log('startHiit')
  playerNum = 0
  playerTimeouts = timeouts.map(val => val * 10)
  timer = setInterval(clockTickHandler(clockCb), 100)
  updateClientHelper(clockCb, true)
}

const end = (condition, clockCb) => {
  resetTimer()
  clockCb({ phase: null, advance: true, condition })
}

const clockTickHandler = (clockCb) => () => {
  let isNewRound = false
  totalTicks += 1
  roundTicks += 1

  if (keepPlayingChess()) {
    if (incrementturnTicks()) {
      return end('Player-Timeout', clockCb)
    }
  } else {
    if (roundExpired()) {
      if (!nextRound()) {
        return end('Rounds-Over', clockCb)
      }
      advanceRound()
      isNewRound = true
    }
  }

  updateClientHelper(clockCb, isNewRound)
}

const updateClientHelper = (clockCb, isNewRound) => {
  const cur = curRound()
  const isChess = isChessPhase(cur)
  const isWorkout = isWorkoutPhase(cur)

  const {
      phase,
      workout,
      reps,
      duration: roundDuration 
    }  = cur

  clockCb({
    isNewRound,
    isChess,
    isWorkout,
    phase,
    workout,
    reps,
    roundDuration,
    roundTicks,
    turnTicks,
    totalTicks,
  })
}



