import React from 'react';
import { compose, lifecycle } from 'recompose';
import { connect } from 'react-redux';

import {siren, whistle} from './audio';

class HiitPanel extends React.Component {

  renderChessPane() {
    const {isChess, isOver, chessCountdown, onClose} = this.props

    const Content = () => 
      !isChess ?
      (
        <span className="Hiit-flash blink">
          Time to HIIT the floor.
        </span>
      )
      : isOver ?
      (
        <button className="Hiit-gameover Hiit-turn-button chess-pattern" onClick={onClose}>
          <span className="Hiit-flash blink">
            Game over !
          </span>
        </button>
      )
      :
      (
        <div className="Hiit-turn-col">
          <span className="Hiit-flash-3x">
            {chessCountdown}
          </span>
        </div>
      )

    const style = `Hiit-panel-box Hiit-chessbox Hiit-rows${ !isChess ? ' chess-pattern' : '' }`
    return (
      <div className={style}>
        <div className="Hiit-columns">
          <Content />
        </div> 
      </div>
    )
  }

  renderWorkoutPane() {
    const {isWorkout, workout, reps, roundCountdown, } = this.props

    const Content = () => 
      !isWorkout ?
      (
        <div className="Hiit-rows">
          <span className="Hiit-flash">
            Next HIIT in
          </span>
          <span className="Hiit-flash-2x">
            {Math.floor(roundCountdown)+1}
          </span>
        </div>
      )
      :
      (
        <div className="Hiit-rows">
          <span className="Hiit-flash">
            {workout}
          </span>
          <span className="Hiit-flash">
            {reps ? `${reps} Reps` : ''}
          </span>
          <span className="Hiit-flash-2x">
            {Math.floor(roundCountdown)+1}
          </span>
        </div>
      )

    return (
      <div className="Hiit-panel-box Hiit-workoutbox Hiit-rows grass-pattern">
        <Content />
      </div>
    )
  }

  render() {
    return (
      <div className="Hiit-panel">
        {this.renderChessPane()}
        {this.renderWorkoutPane()}
      </div>
    )
  }
}

const enhance = compose(
  connect(({hiit}) => hiit),
  lifecycle({
    componentWillReceiveProps({isChess, chessCountdown}) {
      if (this.props.isNewRound) {
        whistle();
      } else if (isChess && chessCountdown <= 4 && Number.isInteger(chessCountdown)) {
        siren();
      }
    }
  })
)

export default enhance(HiitPanel)



