export const siren = () => {
  const audioElement = document.getElementById('siren');
  audioElement.load();
  setTimeout(() => audioElement.play(), 0);
}

export const whistle = () => {
  const audioElement = document.getElementById('beep');
  audioElement.load();
  setTimeout(() => audioElement.play(), 0);
}


