
// helpers

const hunderedMs = duration => duration * 10

const makeRound = (phase, duration, workout, reps) => ({phase, duration: hunderedMs(duration), workout, reps})
const makeChess = (duration = 40) => makeRound('Chess', duration)
const makeWarmup = (workout) => makeRound('Warmup', 15, workout)
const makeChessWorkout = (workout, duration = 20) => makeRound('Chess+Workout', duration, workout)
const makeRepsWorkout = (workout, reps, duration = 20) => makeRound('Workout', duration, workout, reps)

// activities

const warmup = [
  makeWarmup('Jumping-Jacks'),
]

const workouts = [
  makeChessWorkout('Squat'),
  makeRepsWorkout('Burpees', 4),
  makeChessWorkout('Squat', 30),
  makeRepsWorkout('Power-Run', 12),
  makeRepsWorkout('Pushups / Knee-pushups', 6),
  makeRepsWorkout('Knees to Elbows', 8),
  makeRepsWorkout('Pushups / Knee-pushups', 8),
  makeRepsWorkout('Burpees', 6),
]

// hiit-chess rounds

const rounds = workouts.reduce((acc, cur) => {
  acc.push(makeChess())
  acc.push(cur)
  return acc
}, [])

export default [
  ...warmup,
  ...rounds,
  makeChess(60)
]



