import React, { Component } from 'react';
import PropTypes from 'prop-types';
// import { bindActionCreators } from "redux";
import { connect } from 'react-redux';

import { GAME_STATE } from 'chess/utils/constants/constants';
//import * as actions from 'chess/actions';

import Chessboard from 'chess/components/chessboard';
import DumbChessGame from './game';

class SmartChessGame extends Component {
  static propTypes = {
    fen: PropTypes.string.isRequired,
    // from parent
    orientation: PropTypes.string.isRequired,
    validateMove: PropTypes.func.isRequired,
    possibleMoves: PropTypes.func.isRequired,
    makeMove: PropTypes.func.isRequired,
    oppMove: PropTypes.func.isRequired,
    endGame: PropTypes.func.isRequired,
    // form store
    gameState: PropTypes.string.isRequired, // which color won or draw
    gameEnding: PropTypes.string.isRequired, // reason for game ending
    inCheck: PropTypes.bool.isRequired,
    color: PropTypes.string.isRequired,
    moveCount: PropTypes.number.isRequired,
    lastMove: PropTypes.array,
  };

  onMove(from, to) {
    const { color, makeMove } = this.props;
    makeMove(color, from, to);
  }

  isValidMove(from, to) {
    const { color } = this.props;
    const isLive = this.props.gameState === GAME_STATE.LIVE;
    return isLive && this.props.validateMove(from, to, color);
  }

  getPossibleMoves(from) {
    return this.props.possibleMoves(from, this.props.color);
  }

  render() {
    const props = this.props;
    const isLive = props.gameState === GAME_STATE.LIVE;

    const children = (
      <Chessboard
        dnd={isLive}
        lineupOnly={false}
        fen={props.fen}
        colorToMove={props.color}
        orientation={props.orientation}
        highlighted={props.lastMove}
        inCheck={props.inCheck}
        onMove={this.onMove.bind(this)}
        isValidMove={this.isValidMove.bind(this)}
        getPossibleMoves={this.getPossibleMoves.bind(this)}
      />
    );

    return <DumbChessGame>{children}</DumbChessGame>;
  }

  // called after render

  updateGame(prevProps) {
    const { gameState: oldGameState } = prevProps;
    const { gameState: newGameState } = this.props;

    if (newGameState === GAME_STATE.LIVE) {
      // opponent move
      const { color, orientation } = this.props;
      if (color !== orientation) {
        const { oppMove } = this.props;
        oppMove();
      }
    } else if (newGameState !== oldGameState) {
      // just ended
      const { gameEnding, moveCount, endGame } = this.props;
      endGame(newGameState, gameEnding, moveCount);
    }
  }

  componentDidMount() {
    this.updateGame(this.props);
  }

  componentDidUpdate(prevProps) {
    this.updateGame(prevProps);
  }
}

const mapStateToProps = ({ chess }, props) => {
  return {
    ...chess,
    fen: chess.fen ? chess.fen : props.fen,
  };
};

//const mapDispatchToProps = dispatch => bindActionCreators(actions, dispatch);
//export default connect(mapStateToProps, mapDispatchToProps)(SmartChessGame);

export default connect(
  mapStateToProps,
  null
)(SmartChessGame);



