import React, { Component } from 'react'
import PropTypes from 'prop-types'

import { oppositeColor, COLOR } from 'chess/utils/constants/constants'
import ChessLine from 'chess/components/lineup'
import DumbFenPicker from './dumb'

export default class FenPicker extends Component {

  static propTypes = {
    fens: PropTypes.arrayOf(PropTypes.string).isRequired,
    setFenIndex: PropTypes.func.isRequired,
    setOrientation: PropTypes.func,
    orientation: PropTypes.oneOf([COLOR.WHITE, COLOR.BLACK]),
    select: PropTypes.number, // indexed fen ( set -1 for shuffle )
  }

  constructor(props) {
    super(props)
    this.state = {
      index: 0,
      max: 0,
      fen: '',
    }
  }

  componentWillMount() {
    const {fens, select, } = this.props;
    const max = fens.length;
    const shuffle = isNaN(select);
    const index = (shuffle) ? Math.floor(Math.random() * max) : select;
    const fen = fens[index];
    this.setState({
      index,
      max,
      fen,
    });
  }

  componentDidMount() {
    const { select, setFenIndex } = this.props;
    const { index } = this.state;
    if (select !== index) {
      setFenIndex(index);
    };
  }

  nextFen() {
    let {index, } = this.state;
    const max = this.state.max;
    index = (index+1) % max;
    const fen = this.props.fens[index];
    this.setState({
      index,
      fen,
    });
    this.props.setFenIndex(index);
  }

  toggleColor() {
    const theOr = oppositeColor(this.props.orientation);
    this.props.setOrientation(theOr);
  }

  render() {
    const { fen, max, } = this.state;
    const orientation = this.props.orientation || COLOR.WHITE;
    const disableFlip = !this.props.setOrientation;
    const disableNext = (max === 1);

    const children = (
          <ChessLine
            orientation={orientation}
            fen={fen}
          />
      )
    return (
          <DumbFenPicker orientation={orientation}
                      disableFlip={disableFlip}
                      disableNext={disableNext}
                      onNext={this.nextFen.bind(this)}
                      onToggle={this.toggleColor.bind(this)}>
              {children}
          </DumbFenPicker>
      )
  }

}



