import React, { Component } from 'react';
import { COLOR } from 'chess/utils/constants/constants';
import strings from 'app/localize';

export default class DumbFenPicker extends Component {

  render() {
    const { orientation, children,
            disableFlip, disableNext,
            onNext, onToggle } = this.props;
    const opponentColor = (orientation === COLOR.WHITE) ? strings.black : strings.white;

    const FlipButton = () => (
        <button onClick={onToggle} className="secondary button">
          <span>
            <i className="fa fa-lg fa-adjust" aria-hidden="true" />
            {opponentColor}
          </span>
        </button> )

    const ShuffleButton = () => (
        <button onClick={onNext} className="secondary button">
          <span>
            <i className="fa fa-lg fa-chevron-right" aria-hidden="true" />
            {strings.next}
          </span>
        </button> )

    return (
      <div className="fen-picker">
        {children}
        {disableFlip || <FlipButton />}
        {disableNext || <ShuffleButton />}
      </div>
    );
  }

}



