export const COLOR = {
  WHITE: 'white',
  BLACK: 'black',
};

export const SQUARES = [
  'a8', 'b8', 'c8', 'd8', 'e8', 'f8', 'g8', 'h8',
  'a7', 'b7', 'c7', 'd7', 'e7', 'f7', 'g7', 'h7',
  'a6', 'b6', 'c6', 'd6', 'e6', 'f6', 'g6', 'h6',
  'a5', 'b5', 'c5', 'd5', 'e5', 'f5', 'g5', 'h5',
  'a4', 'b4', 'c4', 'd4', 'e4', 'f4', 'g4', 'h4',
  'a3', 'b3', 'c3', 'd3', 'e3', 'f3', 'g3', 'h3',
  'a2', 'b2', 'c2', 'd2', 'e2', 'f2', 'g2', 'h2',
  'a1', 'b1', 'c1', 'd1', 'e1', 'f1', 'g1', 'h1',
];

export const PIECES = [
  'wP', 'wC', 'wN', 'wB', 'wR', 'wQ', 'wK',
  'bP', 'bC', 'bN', 'bB', 'bR', 'bQ', 'bK',
];

export const DND_TYPES = {
  PIECE: 'piece',
};

export const GAME_STATE = {
  LIVE: '?',
  DRAW: 'd',
  WHITE_WON: 'w',
  BLACK_WON: 'b',
};

export const END_GAMEING = {
  CHECKMATE_OR_STALEMATE: 'cs',
  RESIGN: 'r',
  TIMEOUT: 't',
  DRAW_AGREE: 'd',
  // to be implemented
  CHECKMATE: 'c',
  STALEMEATE: 's',
  TOO_FEW_PIECES: 'f',
}

// todo move to adj helper file

export function isWhite(orientation) {
  return orientation === COLOR.WHITE;
}

export function isBlack(orientation) {
  return orientation === COLOR.BLACK;
}

export function oppositeColor(orientation) {
  return isWhite(orientation) ? COLOR.BLACK : COLOR.WHITE;
}

export function deriveOutcomeValue(state, orientation) {
  if (state === GAME_STATE.DRAW) {
    return 0;
  }
  if (isWhite(orientation) && state === GAME_STATE.WHITE_WON) {
    return 1;
  }
  if (isBlack(orientation) && state === GAME_STATE.BLACK_WON) {
    return 1;
  }
  return -1;
}
 


