import { PROFILE_SUCCESS, PROFILE_FAILED,
        LOGIN_SUCCESS, LOGIN_FAILED,
        LOGOUT_ATTEMPT, LOGOUT_SUCCESS } from 'app/actions/constants';

const initialState = {
  isLoggedin: false,
  id: null,
  firstName: '',
  lastName: '',
  email: '',
  pictureLink: '',
  error: '',
  isProfileReady: false,
};

export default function apiHandler(state = initialState, action) {
  switch (action.type) {
    case '@@redux/INIT':
      return { ...state, error: '' };

    case LOGIN_SUCCESS:
      return {
        ...state,
        isLoggedin: true,
        error: '',
      }

    case LOGIN_FAILED:
      return {
        ...initialState,
        isLoggedin: false,
        isProfileReady: false,
        error: action.error,
      };

    // case PROFILE_ATTEMPT:

    case PROFILE_SUCCESS:
      return {
        ...state,
        // redundent/workaround - somehow isLoggedin is not persisted when
        // we login and get redirected to same page - so we check if we are
        // loggedin by calling getProfile on web
        isLoggedin: true,
        isProfileReady: true,
        id: action.id,
        firstName: action.firstName,
        lastName: action.lastName,
        email: action.email,
        pictureLink: action.pictureLink,
        error: '',
      };

    case PROFILE_FAILED:
      return {
        ...initialState,
        isLoggedin: false,
        isProfileReady: false,
        error: action.error,
      };

    case LOGOUT_ATTEMPT:
    case LOGOUT_SUCCESS:
      return initialState; // back to empty state

    // case LOGOUT_FAILED:

    default:
  }
  return state;
}



