import * as axios from 'axios';

import { USER_LOSES, USER_DRAWS } from 'app/actions/constants';

const baseUrl = window.location.origin;

export function registerFbTokenApi(token) {
  return axios.post(`${baseUrl}auth/facebook/token`, {access_token: token})
}

export function getProfileApi() {
  return axios.get(`${baseUrl}v1/profile`);
}

export function logoutUserApi() {
  return axios.post(`${baseUrl}auth/logout`);
}

/* missing: moves, moveCount
 * time has startTime, endTime
 */
export function saveGameApi(quest, fen, level, expertise, reinforce, orientation, outcome) {
    // change outcome to server friendly value
  switch(outcome) {
    case USER_LOSES:
      outcome = 'lose';
      break;
    case USER_DRAWS:
      outcome = 'draw';
      break;
    default: // user wins
      outcome = 'win';
  }
  return axios.post(`${baseUrl}v1/game/`, {
    quest,
    fen,
    level,
    expertise,
    reinforce,
    outcome,
    orientation,
  });
}

export function unlockLevelApi(quest, level) {
  return axios.post(`${baseUrl}v1/quest/${quest}`, { level });
}



