import { LOCK_UI, UNLOCK_UI, SET_ORIENTATION,
       } from 'app/actions/constants'

import { COLOR } from 'chess/utils/constants/constants'

const initialState = {
  isLoading: false,
  apiError: false,
  needsLogin: false,
  orientation: COLOR.WHITE,
}

export default function doAppAction(state = initialState, action) {
  switch (action.type) {
    case '@@redux/INIT':
      return { ...initialState };

    case SET_ORIENTATION: {
      return { ...state, orientation: action.orientation, }
    }

    case LOCK_UI:
      return { ...state, isLoading: true }

    case UNLOCK_UI:
      return { ...state, isLoading: false }

    default:
  }

  return { ...state, apiError: false }}



