// END_GAME, COMPUTER_MOVE_PIECE
import {
  START_GAME,
  RESIGN_GAME,
  DRAW_GAME,
  CLOCK_TIMEOUT,
  MOVE_PIECE,
} from './../actions/constants';

import { COLOR, GAME_STATE, END_GAMEING } from './../utils/constants/constants';

const initialState = {
  fen: '',
  gameState: '', // proxy for no game exist
  gameEnding: '', // end game state
  color: COLOR.WHITE,
  canDraw: false,
  inCheck: false,
  //isDraw: false,
  lastMove: [-1, -1],
  moveCount: 0,
};

export const chess = (state = initialState, action) => {
  switch (action.type) {
    case START_GAME: {
      return {
        ...initialState,
        fen: action.fen,
        gameState: GAME_STATE.LIVE,
        color: COLOR.WHITE,
      };
    }

    case CLOCK_TIMEOUT: {
      const gameState = getStateFromLosingColor(action.color);
      return {
        ...state,
        gameState,
        gameEnding: END_GAMEING.TIMEOUT,
      };
    }

    case RESIGN_GAME: {
      const gameState = getStateFromLosingColor(action.color);
      return {
        ...state,
        gameState,
        gameEnding: END_GAMEING.RESIGN,
      };
    }

    case DRAW_GAME: {
      // end the game
      if (!state.canDraw) {
        // no change
        return state;
      }
      return {
        ...state,
        gameState: GAME_STATE.DRAW,
        gameEnding: END_GAMEING.DRAW_AGREE,
      };
    }

    case MOVE_PIECE: {
      const move = action.move;
      const gameState = getStateFromMove(state.color, move.isMate, state.gameState);
      const gameEnding = move.isMate ? END_GAMEING.CHECKMATE_OR_STALEMATE : '';
      return {
        ...state,
        gameState,
        gameEnding,
        fen: move.fen,
        lastMove: move.move, // clear last move
        color: move.color,
        inCheck: move.inCheck,
        canDraw: move.canDraw,
        moveCount: move.num,
      };
    }

    default:
      return state;
  } // end switch
}

// helper functions

function getStateFromMove(lastMoveColor, isMate, prevState) {
  if (isMate === true) {
    return lastMoveColor === COLOR.WHITE ? GAME_STATE.WHITE_WON : GAME_STATE.BLACK_WON;
  }
  // ignore isDraw for now - just prompt user that
  // they can draw the game
  //if (isDraw === true) {
  //  return GAME_STATE.DRAW;
  //}
  return prevState;
}

function getStateFromLosingColor(color) {
  const state = color === COLOR.WHITE ? GAME_STATE.BLACK_WON : GAME_STATE.WHITE_WON;
  return state;
}



