import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _, { findDOMNode } from 'react-dom';
import { DropTarget } from 'react-dnd';
import classNames from 'classnames';

import { DND_TYPES } from 'chess/utils/constants/constants';

const squareTarget = {
  canDrop(props, monitor) {
    const fromSquare = monitor.getItem().from;
    const toSquare = props.square;
    return props.isValidMove(fromSquare, toSquare);
  },

  drop(props, monitor) {
    const draggedItem = monitor.getItem();
    const fromSquare = draggedItem.from;
    const toSquare = props.square;
    props.onPress(fromSquare);
    props.onPress(toSquare);
  },
};

function collect(connect, monitor) {
  return {
    connectDropTarget: connect.dropTarget(),
    isOver: monitor.isOver(),
  };
}

@DropTarget(DND_TYPES.PIECE, squareTarget, collect)
export default class DumbSquare extends Component {
  static propTypes = {
    isBlack: PropTypes.bool.isRequired,
    connectDropTarget: PropTypes.func.isRequired,
    onPress: PropTypes.func,
  }

  render() {
    const { isBlack, isOver, onPress, files,
            oppMove, isSelected, canMoveTo, isInDanger,
            connectDropTarget, children } = this.props;

    const classes = classNames({
      'react-chessboard-square': true,
      'react-chessboard-square--files-4': files === 4,
      'react-chessboard-square--files-5': files === 5,
      'react-chessboard-square--files-6': files === 6,
      'react-chessboard-square--files-8': files === 8,
      'react-chessboard-square--black': isBlack,
      'react-chessboard-square--white': !isBlack,
      'react-chessboard-square--over': isOver,
      'react-chessboard-square--opp-move-from': oppMove === 1,
      'react-chessboard-square--opp-move-to': oppMove === 2,
      'react-chessboard-square--selected': isSelected,
      'react-chessboard-square--danger': isInDanger,
      'react-chessboard-square--possible-move': canMoveTo,
    });

    return (
      <div
        className={classes} onClick={onPress}
        ref={instance => connectDropTarget(findDOMNode(instance))}
      >
        {children}
      </div>
    );
  }
}



