import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _, { findDOMNode } from 'react-dom';
import classNames from 'classnames';
import { DragSource } from 'react-dnd';

import { DND_TYPES } from 'chess/utils/constants/constants';

const isCordovaApp = !!window.cordova;

const pieceSource = {
  canDrag(props) {
    return props.dnd && props.canMove;
  },

  beginDrag(props) {
    return {
      from: props.square,
      piece: props.piece,
    };
  },
};

function collect(connect, monitor) {
  return {
    isDragging: monitor.isDragging(),
    connectDragSource: connect.dragSource(),
  };
}

@DragSource(DND_TYPES.PIECE, pieceSource, collect)
export default class DumbPiece extends Component {
  static propTypes = {
    isDragging: PropTypes.bool.isRequired,
    onPress: PropTypes.func.isRequired,
  };

  render() {
    const { piece, canMove, isDragging, onPress, connectDragSource } = this.props;
    const classes = classNames({
      'react-chessboard-piece': true,
      [`react-chessboard-piece--${piece}`]: true,
      'react-chessboard-piece--can-move': canMove,
      'react-chessboard-piece--dragging': isDragging,
    });

    if (isCordovaApp) {
      return (<div className={classes} onClick={onPress} />);
    }

    return (
      <div
        className={classes} onClick={onPress}
        ref={instance => connectDragSource(findDOMNode(instance))}
      />
    );
  }
}



