import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'

import { localActions, chessActions, } from 'app/actions'
import ChessGame from 'chess/components/game'
import HiitPanel from 'hiit/components/HiitPanel'
import HiitWorkoutOverlay from 'hiit/components/HiitWorkoutOverlay'
import { validateMove } from 'chess/middlewares'

import DumbGame from './game'

class SmartGame extends Component {

  static propTypes = {
    isOver: PropTypes.bool.isRequired,
  }

  validateMove(from, to, color) {
    return validateMove(from, to)
  }

  possibleMoves(from, color) {
    const moves = validateMove(from) || []
    return moves
  }

  resetGame() { // next screen
    this.props.resetLocal()
  }

  onMove(color, from, to) {
    this.props.makeMove(from, to)
  }

  onOppMove() {
    this.props.aiMove()
  }

  onEnd(winnerSide, endingReason, numMoves) {
    console.log('onEnd', winnerSide, endingReason, numMoves)
    this.props.endLocal(winnerSide, endingReason, numMoves)
  }

  render() {
    const { turn, } = this.props

    const theKey = `chessgame-local-${turn}`

    // fen is not required - it comes from reducer internal to chess
    const chessGame = (
        <ChessGame key={theKey}
            orientation={turn}
            validateMove={this.validateMove.bind(this)}
            possibleMoves={this.possibleMoves.bind(this)}
            makeMove={this.onMove.bind(this)}
            oppMove={this.onOppMove.bind(this)}
            endGame={this.onEnd.bind(this)}
        />
      )
    
    const hiitPanel = <HiitPanel onClose={this.resetGame.bind(this)} />

    const hiitWorkout = <HiitWorkoutOverlay />

    return (
        <DumbGame>
          {chessGame}
          {hiitPanel}
          {hiitWorkout}
        </DumbGame >
      )
  }

}

const mapStateToProps = ({ local, }) => {
  const { fen, outcome, turn, } = local
  return {
    fen,
    turn,
    outcome,
  }
}

const mapDispatchToProps = dispatch => bindActionCreators({
  ...localActions,
  ...chessActions,
}, dispatch)

export default connect(mapStateToProps, mapDispatchToProps)(SmartGame)



