const baseUrl = window.location.origin
const fbLoginUrl = `${baseUrl}auth/facebook/`

function webFbLogin(successUrl) {
  const promise = new Promise((resolve, reject) => {
    setTimeout(() => {
      window.open(
         `${fbLoginUrl}?r=${successUrl}`,
         '_self',
         'location=yes'
      );
    }, 10); // after other code has run (being extra safe)

    const fakeError = new Error('Fake error: web login throws a reject')
    reject(fakeError)
  })
  return promise;
}

export function fbLoginApi(successUrl) {
  successUrl = successUrl || '';
  return webFbLogin(successUrl);
}

export function fbLogoutApi() {
  // TODO fb logout for web
}



