import {
    // match lifecycle
    LOCAL_RESET,
    LOCAL_FEN,
    LOCAL_START,
    LOCAL_END,
    // match events
    LOCAL_PAUSE,
    LOCAL_FLIP,
    // misc
    LOGOUT_ATTEMPT, LOGIN_ATTEMPT,
    // outcomes
    USER_IS_PLAYING, // USER_DRAWS, USER_WINS, USER_LOSES,
    // states of active chess game
    GAME_READY,
    START_GAME,
    END_GAME,
  } from 'app/actions/constants'

import { deriveOutcome } from 'app/utils/games'
import { COLOR } from 'chess/utils/constants/constants'

const initialOutcome = USER_IS_PLAYING
const initialTimer = 3 * 60 // time per player - 3 mins

const initialState = {
  // game settings
  fenIndex: 0,
  //orientation: COLOR.WHITE,
  timeout: initialTimer,
  // current state
  progress: GAME_READY,
  // post game start
  fen: '',
  // time
  turn: COLOR.WHITE, // for opponent to make move
  whiteClock: 0,
  blackClock: 0,
  lastMoveTs: 0,
  lastPauseTs: 0,
  isPaused: false,
  // ending
  state: null, // white wins, black wins, draw, in progress
  outcome: initialOutcome, // user base outcome (USER_WINS, USER_LOSES, USER_DRAWS)
}

export default function localState(state = initialState, action) {

  switch (action.type) {
    case '@@redux/INIT': {
      return {
        ...initialState,
        fenIndex: state.fenIndex || 0,
        timeout: state.timeout || initialTimer,
      }
    }

    case LOGIN_ATTEMPT:
    case LOGOUT_ATTEMPT: {
      return initialState
    }

    case LOCAL_START: {
      const {fen, timeout, } = action
      return {
        ...state,
        progress: START_GAME,
        fen,
        //orientation: COLOR.WHITE,
        timeout,
        // init clock
        whiteClock: 0,
        blackClock: 0,
        lastMoveTs: Date.now(),
      }
    }

    case LOCAL_PAUSE: {
      const { turn: nextTurn } = action
      const { turn: curTurn } = state

      if (curTurn === nextTurn) { // ignore
        return state
      }

      return {
        ...state,
        lastPauseTs: Date.now(),
        isPaused: true,
      }
    }

    case LOCAL_FLIP: {
      const { turn: nextTurn } = action
      const { turn: curTurn, lastPauseTs } = state

      if (curTurn === nextTurn) { // ignore
        return state
      }

      let result = {
        ...state,
        lastMoveTs: Date.now(),
        turn: nextTurn,
        isPaused: false,
      }

      const delta = lastPauseTs - state.lastMoveTs
      if (curTurn === COLOR.WHITE) { // this move was by me
        result.whiteClock += delta
        console.log('White Clock : ', result.whiteClock)
      } else { // this move was by opponent
        result.blackClock += delta
        console.log('Black Clock : ', result.blackClock)
      }

      //console.log("Clock delta: ", delta)
      return result
    }

    case LOCAL_END: {
      // TODO end clocks, outcome
      const orientation = COLOR.WHITE;
      const outcome = deriveOutcome(action.state, orientation)
      return {
        ...state,
        progress: END_GAME,
        state: action.state,
        outcome,
      }
    }

    case LOCAL_RESET: {
      // preserve user settings on cancel and match reset
      const { fen, timeout, } = state;
      return {
        ...initialState,
        progress: GAME_READY,
        fen,
        timeout,
      }
    }

    case LOCAL_FEN: {
      return {
        ...state,
        fenIndex: action.fenIndex,
      }
    }

/*
    case LOCAL_TIMEOUT: {
      const timeout = action.timeout
      return {
        ...state,
        timeout,
      }
    }
*/

    default:
  }

  return state
}



