
import config from "appConfig";

import { COLOR, deriveOutcomeValue }
        from 'chess/utils/constants/constants';

import { USER_WINS, USER_LOSES, USER_DRAWS, } from 'app/actions/constants';

export const HALF_LEVELS = config.hardNumLevels;
const REINFORCE_HALF_GAME = config.hardReinforceOptions;
const DIFFY_HALF_GAME = config.hardAIDepths;
const HALF_GAMES = config.hardGames;

export const EASY_LEVELS = config.easyNumLevels;
const REINFORCE_EASY_GAME = config.easyReinforceOptions;
const DIFFY_EASY_GAME = config.easyAIDepths;
const EASY_GAMES = config.easyGames;

export const MATCH_GAMES = config.matchGames;
export const MATCH_DEFAULT_FEN = MATCH_GAMES[0];

// helper functions

function LOAD_STAGES(max, data, diffMap, reinfMap) {
  // initialize
  let result = {};
  for(let num = 1; num <= max; num++) {
    result[num] = [];
  }

  // helper function to add a stage to final output
  const addStage = (stageNum, fen) => {
    const difficulty = diffMap[stageNum];
    const reinforcePieces = reinfMap[stageNum];
    result[stageNum].push({fen, difficulty, reinforcePieces});
  }

  // load
  for (let record of data) {
    const { fen } = record;
    for (let stage of record.stages) {
      const {at, above} = stage;
      if (!!at) { // at
        addStage(at, fen);
      }
      else if (!!above) { // above
        for(let _at = above; _at <= max; _at++) {
          addStage(_at, fen);
        }
      }
    }
  }
  return result;
}

// consumable data-structure

export const QUEST_GAMES = {
  'half': LOAD_STAGES(HALF_LEVELS, HALF_GAMES, DIFFY_HALF_GAME, REINFORCE_HALF_GAME),
  'easy': LOAD_STAGES(EASY_LEVELS, EASY_GAMES, DIFFY_EASY_GAME, REINFORCE_EASY_GAME),
  };

