// randomly adds a piece (from pieces argument) to the fen
// and reinforces the fen

export function reinforceFen(oldFen, orientation, pieces) {
  const files = 4; // TODO: this should not be hardcoded

    // pick one of the possible pieces

  const pieceIdx = Math.floor(Math.random() * pieces.length)
  const piece = (orientation === COLOR.WHITE)
                   ? pieces[pieceIdx].toUpperCase()
                   : pieces[pieceIdx].toLowerCase();

    // location of the piece in file

  const placement = Math.floor(Math.random() * files); // reinforce in first 4 slots

    // replace string... replace 0 with nothing

  const row = `${placement}${piece}${files-placement-1}`.replace('0', '');

    // reinfoce fen with piece

    // search and replace from last

  let newFen = oldFen.split('').reverse().join('')
                     .replace(`${files}`, row)
                     .split('').reverse().join(''); // reverse again

  return newFen;
}

