export function deriveOutcome(state, orientation) {
  const r = deriveOutcomeValue(state, orientation);
  const outcome = (r===0) ? (USER_DRAWS) :
                  (r===1) ? (USER_WINS) : (USER_LOSES);
  return outcome;
}

