import * as HiitEngine from './lib';

import {
  HIIT_START,
  HIIT_END,
  HIIT_TICK,
  HIIT_CHESS_TICK,
  HIIT_WORKOUT_TICK,
  HIIT_TIMEOUT
} from './constants';

import {
  START_GAME,
  END_GAME,
  MOVE_UI,
  MOVE_AI,
} from 'chess/actions/constants';

export const hiitMiddleware = store => next => action => {
  console.log('Action:' + action.type)
  switch (action.type) {
    case START_GAME: {
      // move to chess
      let { timeout } = action;
      HiitEngine.startHiit([timeout, 0], clockHandlerCb(store)(next))
      next({type: HIIT_START, timeout})
      break;
    }

    case END_GAME: {
      HiitEngine.resetHiit()
      next({type: HIIT_END})
      break;
    }

    case MOVE_AI:
    case MOVE_UI: {
      HiitEngine.endTurn()
      break;
    }

    default:
  }

  return next(action);
};

const clockHandlerCb = store => next => round => {
  const {
    isNewRound,
    isChess,
    isWorkout,
    phase,
    workout,
    reps,
    roundDuration,
    roundTicks,
    turnTicks,
    totalTicks } = round
  
  if (!phase) {
    store.dispatch({type: END_GAME})
    next({ type: HIIT_TIMEOUT })
    return
  }

  next({
    type: HIIT_TICK,
    isNewRound,
    totalTicks,
    roundCountdown:  roundDuration - roundTicks,
  })
  
  if (isChess) {
    next({
      type: HIIT_CHESS_TICK,
      turnTicks,
    })
  }

  if (isWorkout) {
    next({
      type: HIIT_WORKOUT_TICK,
      phase,
      workout,
      reps,
    })
  }
}


