import {
    // match lifecycle
    LOCAL_RESET,
    LOCAL_FEN,
    LOCAL_START,
    LOCAL_END,
    // match events
    LOCAL_PAUSE,
    LOCAL_FLIP,
  } from './constants'

export function newLocal(fen, timeout) {
  return { type: LOCAL_START, fen, timeout }
}

export function endLocal(state, ending, num) {
  return { type: LOCAL_END, state, ending, num }
}

export function resetLocal() {
  return { type: LOCAL_RESET, }
}

export function setLocalFenIndex(fenIndex) {
  return { type: LOCAL_FEN, fenIndex, }
}

export function localPause(turn) {
  return { type: LOCAL_PAUSE, turn, }
}

export function flipLocal(turn) {
  return { type: LOCAL_FLIP, turn, }
}



