import React, { Component } from 'react';
import Toggle from 'react-toggle';

export default class DumbDashboard extends Component {

  render() {
    const { timerLabel, children, isChecked,
      onNew, togTimeout, } = this.props;

    const block = {display: 'block', marginTop: '10px'};
    const inherit = {display: 'inherit'};

    const toggleButton = (
        <label style={block}>
          <span style={inherit}>
            <Toggle
                defaultChecked={isChecked}
                icons={false}
                onChange={togTimeout} />
          </span>
        </label> )

    const actionButton = (
        <button onClick={onNew}
                className="primary main-bottom-button button">
          <span>
            Play HIIT Chess
          </span>
        </button> )

    const moveDuration = (
        <div style={{ marginTop: 20 }}>
          <h2 style={{ display: 'inline-block' }}>{timerLabel}</h2>
          <span> sec Turn</span>
        </div> )

    return (
      <div className="dashboard-background">
        <div className="dashboard">
          <div className="header">
            <h1>HIIT Chess</h1>
            <h3>A game for your body and mind.</h3>
          </div>
          {children}
          {moveDuration}
          {toggleButton}
          {actionButton}
          <div className="footer">
            <h5>A variation of <a target="blank" href="https://halfchess.com" >Halfchess.com </a></h5>
          </div>
        </div>
      </div>
    );
  }

}



