import {
  HIIT_START,
  HIIT_END,
  HIIT_TICK,
  HIIT_CHESS_TICK,
  HIIT_WORKOUT_TICK,
  HIIT_TIMEOUT
} from './constants';

const initialState = {
  timeout: 0,
  chessCountdown: 0,
  roundCountdown: 0,
  isChess: false,
  isWorkout: false,
  isOver: false,
  timedOut: false,
  flashNewRound: false,
}

export const hiit = (state = initialState, action) => {
  switch (action.type) {

    case HIIT_START: {
      return {
        ...initialState,
        timeout: action.timeout * 10,
      }
    }

    case HIIT_END: {
      return {
        ...state,
        isOver: true,
      }
    }

    case HIIT_TIMEOUT: {
      return {
        ...state,
        timedOut: true,
      }
    }

    case HIIT_TICK: {
      const {
        roundCountdown,
        isNewRound,
      } = action

      return {
        ...state,
        roundCountdown: Math.ceil(roundCountdown / 10 - 1),
        isNewRound,
        ...isNewRound ? {isWorkout: false, isChess: false} : {}
      }

    }

    case HIIT_CHESS_TICK: {
      const {turnTicks} = action
      const left = state.timeout - turnTicks
      const chessCountdown = (left > 50) ? Math.floor(left / 10) : (left / 10)
      return {
        ...state,
        isChess: true,
        chessCountdown,
      }
    }

    case HIIT_WORKOUT_TICK: {
      const {phase, workout, reps,} = action
      return {
        ...state,
        isWorkout: true,
        phase,
        workout,
        reps,
      }
    }

    default:
  } // end switch

  return state;
}



